import React, {Component} from "react";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import { Link } from "react-router-dom"

class TableForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
            tables: [],
            selectedTable: ""
        }
    }


    componentDidMount() {
        fetch("https://sql-tables.herokuapp.com/api/tables")
            .then(res => res.json())
            .then(data => {
                this.setState({
                    loaded: true,
                    tables: data,
                    selectedTable: data[0].name
                });
            });
    }

    handleChange = (event) => {
        const {name, value} = event.target;
        this.setState({
            [name]: value
        });
    }

    render() {
        const selectOptions = this
            .state
            .tables
            .map(table => <option key={table.id} value={table.name}>{table.name}</option>)

        const tableSelect = (
            <Form.Group as={Row} controlId="formSelectTable">
                <Form.Label column sm={2}>Select table to be shown: </Form.Label>
                <Col sm={4} lg={2}>
                    <Form.Control
                        as="select"
                        name="selectedTable"
                        value={this.state.selectedTable}
                        onChange={this.handleChange}
                    >
                        {selectOptions}
                    </Form.Control>
                </Col>
            </Form.Group>
        );



        return (
            <div className="basic-spacing">
                {
                    this.state.loaded ?
                        <Form onSubmit={event => event.preventDefault()} id="form-table">
                            {tableSelect}
                            <Link to={`/tables/${this.state.selectedTable}`}>
                                <Button type="submit">Show table</Button>
                            </Link>
                        </Form>
                        :
                        <div>
                            Loading table names...
                        </div>
                }
            </div>
        );
    }

}

export default TableForm;