import React, {Component} from "react";
import TableForm from "./TableForm";
import TableViewer from "./TableViewer";
import { Route } from "react-router-dom";

class Main extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
            tables: [],
            selectedTable: ""
        };
    }

    render() {
        return (
            <main>
                <Route exact={true} path={"/"} component={TableForm} />
                <Route exact={true} path={"/tables/:tableName"} component={TableViewer} />
            </main>
        );
    }
}

export default Main;