import React, {Component} from "react";
import Table from "react-bootstrap/Table";
import {Link} from "react-router-dom";
import {Button} from "react-bootstrap";
import * as SockJS from "sockjs-client";
import * as Stomp from "stompjs";
import TableRow from "./TableRow";
import AddTableRow from "./AddTableRow";

class TableViewer extends Component {

    stompClient = null;

    constructor(props) {
        super(props);
        this.state = {
            contentLoaded: false,
            tableContent: [],
            isAddingNewRow: false
        }
    }

    componentDidMount() {
        const tableName = this.props.match.params.tableName;
        this.fetchTableData(`https://sql-tables.herokuapp.com/api/${tableName}`);
        this.websocketConnection(
            'https://sql-tables.herokuapp.com/ws',
            `/topic/${tableName}`);
    }

    fetchTableData = (url) => {
        fetch(url)
            .then(res => res.json())
            .then(data => {
                this.setState({
                    contentLoaded: true,
                    tableContent: data
                });
            });
    }

    websocketConnection = (url, subscription) => {
        let socket = new SockJS(url);
        this.stompClient = Stomp.over(socket);
        this.stompClient.connect({}, () => {
            // Delegate actions to adequate methods
            this.stompClient.subscribe(`${subscription}/add`, (msg) => this.addRow(JSON.parse(msg.body)));
            this.stompClient.subscribe(`${subscription}/delete/{id}`, (msg) => this.deleteRow(JSON.parse(msg.body)));
            this.stompClient.subscribe(`${subscription}/update/{id}`, (msg) => this.updateRow(JSON.parse(msg.body)));
        });
    }

    addRow = (newRow) => {
        this.setState((oldState) => {
            const index = oldState.tableContent.findIndex((row) => row.id === newRow.id);
            if(index !== -1) {
                oldState.tableContent[index] = newRow
                return {tableContent: oldState.tableContent}
            } else {
                const newContent = oldState.tableContent.concat(newRow);
                return {tableContent: newContent};
            }
        })
    }

    updateRow = (updatedRow) => {
        this.setState((oldState) => {
            const index = oldState.tableContent.findIndex((row) => row.id === updatedRow.id);
            oldState.tableContent[index] = updatedRow;
            return {tableContent: oldState.tableContent};
        })
    }

    deleteRow = (id) => {
        this.setState((oldState) => {
            const index = oldState.tableContent.findIndex((row) => row.id === id);
            oldState.tableContent.splice(index, 1);
            return {tableContent: oldState.tableContent};
        })
    }

    toggleAddingRow = () => {
        this.setState(oldState => {
            return {isAddingNewRow: !oldState.isAddingNewRow}
        });
    }

    onAddClicked = (newRow) => {
        this.stompClient.send(`/app/${this.props.match.params.tableName}/add`, {}, JSON.stringify(newRow));
    }

    onDeleteClicked = (id) => {
        this.stompClient.send(`/app/${this.props.match.params.tableName}/delete/${id}`, {});
    }

    onUpdateClicked = (id) => {
        this.stompClient.send(
            `/app/${this.props.match.params.tableName}/update/${id}`,
            {},
            JSON.stringify(this.state.tableContent.find(row => row.id === id))
        );
    }

    onCellChange = (event, rowId) => {
        const {name, value} = event.target;
        this.setState((oldState) => {
            const index = oldState.tableContent.findIndex((row) => row.id === rowId);
            let newRow = oldState.tableContent[index];
            newRow = {
                ...newRow,
                [name]: value
            }
            oldState.tableContent[index] = newRow;
            return {tableContent: oldState.tableContent};
        })
    }

    render() {
        if(this.state.contentLoaded) {
            const columns = Object.getOwnPropertyNames(this.state.tableContent[0]);
            const columnNames = columns.map(col => <th>{col}</th>);  // Head row
            const contentRows = this.state.tableContent.map(
                row => <TableRow key={row.id} data={row} onDelete={this.onDeleteClicked} onValueChanged={this.onCellChange} onUpdate={this.onUpdateClicked} resetValues={this.updateRow}/>
                );

            return (
                <div className="basic-spacing">
                    <div>
                        <Link to={"/"}>
                            <Button>Back</Button>
                        </Link>
                    </div>
                    <Table striped hover className="last-clear border-side show-on-hover">
                        <caption style={{captionSide: "top"}}>{this.state.selectedTable}</caption>
                        <thead>
                        <tr>
                            {columnNames}
                        </tr>
                        </thead>
                        <tbody>
                            {contentRows}
                            {
                                this.state.isAddingNewRow &&
                                <AddTableRow cells={this.state.tableContent[0]}
                                             onAddClicked={this.onAddClicked}
                                             toggleAddingRow={this.toggleAddingRow}
                                />
                            }
                        </tbody>
                    </Table>
                    <div>
                        <Button onClick={this.toggleAddingRow}>Add</Button>
                    </div>
                </div>
            );
        } else {
            return (
                <div className="basic-spacing">
                    <div>
                        <Link to={"/"}>
                            <Button>Back</Button>
                        </Link>
                    </div>
                    <p className="basic-spacing">
                        Loading content...
                    </p>
                </div>
            );
        }
    }

}

export default TableViewer;