import React, {Component} from "react";
import {Button} from "react-bootstrap";

class TableRow extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isEditing: false,
            defaultValues: this.props.data
        }
    }

    toggleEditing = () => {
        if(this.state.isEditing) { // Reset values when editing is canceled
            this.props.resetValues(this.state.defaultValues);
        }
        this.setState((oldState) => {
            return {
                isEditing: !oldState.isEditing,
            };
        });
    }

    render() {
        const data = this.props.data;
        const columns = Object.keys(this.props.data);
        const cells = Object.values(data).map((cell, index) => {
            return this.state.isEditing ?
                <td key={index}>
                    <input value={cell}
                           name={columns[index]}
                           size={cell === null || cell.toString().length === 0 ? 1 : cell.toString().length}
                           onChange={(event) => this.props.onValueChanged(event, data.id)}
                    />
                </td>
                :
                <td key={index}>{cell}</td>
        })
        const leftButton = this.state.isEditing ?
            <Button variant="success"
                    size="sm"
                    style={{padding: "3px", paddingLeft: "8px", paddingRight: "8px"}}
                    onClick={(event => {
                        event.preventDefault();
                        this.setState({isEditing: false});
                        this.props.onUpdate(data.id);
                        })
                    }
            >
                Send
            </Button>
            :
            <Button variant="outline-primary"
                    size="sm"
                    style={{padding: "3px", paddingLeft: "8px", paddingRight: "8px"}}
                    onClick={this.toggleEditing}
            >
                Edit
            </Button>
        const rightButton = this.state.isEditing ?
            <Button variant="secondary"
                    size="sm"
                    style={{padding: "3px", paddingLeft: "8px", paddingRight: "8px", marginLeft: "3px"}}
                    onClick={this.toggleEditing}
            >
                Cancel
            </Button>
            :
            <Button variant="outline-danger"
                    size="sm"
                    style={{padding: "3px", paddingLeft: "8px", paddingRight: "8px", marginLeft: "3px"}}
                    onClick={() => this.props.onDelete(data.id)}
            >
                Delete
            </Button>


        return (
            <tr>
                {cells}
                <td>
                <span>
                    {leftButton}
                    {rightButton}
                </span>
                </td>
            </tr>);
    }

}

export default TableRow;