import React, {Component} from "react";
import {Button} from "react-bootstrap";

class AddTableRow extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: this.newObjectFromKeys(this.props.cells)
        }
    }

    newObjectFromKeys = (object) => {
        let newObject = {}
        Object.keys(object).forEach(key => { newObject[key] = "" });
        return newObject;
    }

    onChange = (event) => {
        const {name, value} = event.target;
        this.setState(oldState => {
            oldState.data[name] = value;
            return oldState;
        })
    }

    render() {
        const cells = Object.keys(this.state.data).map(
            name => <td>
                <input
                    size={this.state.data[name].length || 1}
                    name={name}
                    onChange={this.onChange}
                    value={this.state.data[name]}
                />
            </td>)
        const leftButton =
            <Button variant="success"
                    size="sm"
                    style={{padding: "3px", paddingLeft: "8px", paddingRight: "8px"}}
                    onClick={(event) => {
                        event.preventDefault();
                        this.props.toggleAddingRow();
                        this.props.onAddClicked(this.state.data);
                    }}
            >
                Send
            </Button>
        const rightButton =
            <Button variant="secondary"
                    size="sm"
                    style={{padding: "3px", paddingLeft: "8px", paddingRight: "8px", marginLeft: "3px"}}
                    onClick={this.props.toggleAddingRow}
            >
                Cancel
            </Button>
        return (
            <tr>
                {cells}
                <td>
                    {leftButton}
                    {rightButton}
                </td>
            </tr>
        );
    }

}

export default AddTableRow;